      handler: function handler() {
        var _this3 = this;

        // 页面停留时间统计——正常路由跳转
        var timeInterval = new Date().getTime() - this.baseTimeStamp;
        var userAgent = window.navigator.userAgent; // 区别浏览器 和平台

        var isFireFox = /(?:Firefox)/.test(userAgent);
        var isTablet = /(?:iPad|PlayBook)/.test(userAgent) || isAndroid && !/(?:Mobile)/.test(userAgent) || isFireFox && /(?:Tablet)/.test(userAgent);
        var isAndroid = /(Android)/i.test(userAgent);
        var isIPhone = /(iPhone|iPod|iOS)/i.test(userAgent);
        var device = 1;

        if (isIPhone) {
          // iphone
          device = 4;
        } else if (isTablet) {
          // 平板
          device = 2;
        } else if (isAndroid) {
          // andriod
          device = 3;
        } else {
          // pc
          device = 1;
        }

        if (process.env.NODE_ENV != "development") {
          this.$apiCall("api.Visit.add", {
            lang: window.navigator.language,
            device: window.navigator.userAgent,
            deviceHard: device || 1,
            nowUrl: window.location.href,
            object: "12001",
            objectId: timeInterval
          }, function (r) {
            // 路由跳转后 基本时间重置
            _this3.baseTimeStamp = new Date().getTime();
          });
        }

        if (this.$route.name == "Home" && this.categories) {
          setTimeout(function () {
            runMegamenu();
          }, 200);
        }

        if (!this.$route.meta.noCheck) {
          this.checkLogin();
        }

        if (this.$route.name == "ordersManagement") {
          setTimeout(function () {
            _this3.getCustomerAllOrderCnt();
          }, 200);
        }

        if (this.shopInfo) {
          this.getShopConfig();
        }
      },
      deep: true
    },
