    init: function init() {
      var _this19 = this;

      if (!this.$store.state.userInfo) {
        setTimeout(function () {
          _this19.init();
        }, 1000);
        return;
      }

      var reg = /^localhost:/;

      if (reg.test(window.location.host)) {
        this.path = "ws://192.168.2.24:1224/";
      } else {
        var arr = window.location.host.split(".");
        this.path = window.location.host.toLowerCase().indexOf("sandbox") >= 0 ? "wss://sandboxwww.".concat(/^(\w+\.)+[a-zA-Z]+$/.test(location.hostname) ? arr[arr.length - 2] : "ourmall", ".com/ws") : "wss://www.".concat(/^(\w+\.)+[a-zA-Z]+$/.test(location.hostname) ? arr[arr.length - 2] : "ourmall", ".com/ws");
      }

      if (typeof WebSocket !== "undefined") {
        // 实例化socket
        this.socket = new WebSocket(this.path, ["auth", this.$store.state.userInfo.id, this.$store.state.userInfo.apiUserToken]); // 监听socket连接

        this.socket.onopen = this.open; // 监听socket错误信息

        this.socket.onerror = this.error; // 监听socket消息

        this.socket.onmessage = this.getMessage;
      }
    },
    open: function open() {
      var _this20 = this;

      setInterval(function () {
        _this20.send();
      }, 28000);
    },
    send: function send() {
      if (this.$store.state.userInfo) {
        if (this.socket.readyState == 1) {
          this.socket.send(JSON.stringify({
            userId: this.$store.state.userInfo.id,
            ac: "ping"
          }));
        } else {
          this.reconnect();
        }
      }
    },
    error: function error() {
