    reconnect: function reconnect() {
      var _this21 = this;

      if (this.lockReconnect) return;
      this.lockReconnect = true;
      setTimeout(function () {
        try {
          if (_this21.$store.state.userInfo) _this21.socket = new WebSocket(_this21.path, ["auth", _this21.$store.state.userInfo.id, _this21.$store.state.userInfo.apiUserToken]);
        } catch (err) {
          _this21.reconnect();
        }

        _this21.lockReconnect = false;
      }, 2000);
    },
