    //  倒计时
    countTime: function countTime(timer) {
      var _this23 = this;

      // console.log(timer)
      var that = this;

      if (!timer) {
        return;
      }

      this.countTimer = setInterval(function () {
        var date = new Date();
        var now = date.getTime();
        var end = timer * 1000;
        var leftTime = end - now; //时间差
        // console.log(leftTime)

        var d, h, m, s;

        if (leftTime >= 0) {
          d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
          h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
          m = Math.floor(leftTime / 1000 / 60 % 60);
          s = Math.floor(leftTime / 1000 % 60);
          s = s < 10 ? "0" + s : s;
          m = m < 10 ? "0" + m : m;
          h = h < 10 ? "0" + h : h;
          _this23.countDown = d + " Day : " + h + " h : " + m + " m : " + s + " s";
        } else {
          _this23.annDialogVisible = false;
          clearInterval(_this23.countTimer);
        }
      }, 1000);
    },
