          if (_this24.announce.length == 0) {
            return false;
          } else {
            _this24.annDialogVisible = true;
          }

          _this24.announce.content = encodeURI(_this24.announce.content).replace(/&nbsp;/g, " ");
          _this24.announce.content = decodeURI(_this24.announce.content);

          _this24.countTime(r.Data.Results.validTimeFrom);
        }
      });
