    handleCommand: function handleCommand(command) {
      switch (command) {
        case "home":
          window.location.href = "/";
          break;

        case "rewards":
          this.openDialogInvitationRewards();
          break;

        case "exit":
          localStorage.removeItem("c_wantVisitPath");
          this.$Logout("/");
          break;

        case "profile":
          this.$router.push({
            name: "profile"
          });
          break;

        case "wallet":
          if (this.baseUrl != "/my") return;
          this.$router.push({
            name: "wallet"
          });
          break;

        case "settings":
          this.$router.push({
            name: "settings"
          });
          break;
      }
    }
